/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem 0;
  margin-bottom: 2.5rem 0;
}

.centered-content {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.yellow-button {
  background-color: #f2be01;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border-radius: 7px;
  border: none;
  text-transform: uppercase;
}
.yellow-button:hover {
  background-color: #d9aa01;
  cursor: pointer;
}

.yellow-block-button {
  background-color: #f2be01;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: block;
  border-radius: 7px;
  border: none;
  text-transform: uppercase;
}
.yellow-block-button:hover {
  background-color: #d9aa01;
  cursor: pointer;
}

.green-button {
  background-color: #8adb00;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border-radius: 7px;
  border: none;
  text-transform: uppercase;
}
.green-button:hover {
  background-color: #7ac200;
  cursor: pointer;
}

.dark-mode {
  background-color: #121212;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #2b2b2b;
  border: 1px solid #2b2b2b;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #8adb00;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .about-text blockquote {
  color: #ffffff;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #2b2b2b;
  border: 1px solid #2b2b2b;
  color: #ffffff;
}

/** Intern Pages **/
@media (min-width: 768px) {
  .about-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
} /** UI **/
@media (min-width: 768px) {
  .ads-container {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.ads {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
  border-radius: 10px;
}
.ads img {
  border-radius: 10px 10px 0 0;
}
.ads .ads-content {
  padding: 2rem;
}
.ads h3,
.ads p {
  margin: 0;
  text-align: center;
}
.ads p.price {
  color: #8adb00;
  font-weight: 700;
  font-size: 3rem;
  text-align: left;
  margin-left: 2rem;
}
.ads .icons-features {
  display: flex;
  justify-content: center;
  list-style: none;
  padding-left: 4rem;
  margin: 2rem 0;
}
.ads .icons-features li {
  flex: 1;
  display: flex;
}
.ads .icons-features li img {
  height: 30px;
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.ads .icons-features li p {
  font-weight: 900;
}

.contact-image {
  background-image: url("../img/find.jpg");
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5rem 0;
}
.contact-image h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #ffffff;
  margin: 0;
}
.contact-image p {
  font-size: 1.8rem;
  color: #ffffff;
  margin-bottom: 6rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #2b2b2b;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem 0;
}
.footer .navegation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
  font-size: 14px;
}

.form p {
  font-size: 1.4rem;
  color: #2b2b2b;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #2b2b2b;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .about-icons {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 9rem;
  width: 9rem;
  display: block;
  margin: 0 auto;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .inferior-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 4rem;
    margin-bottom: 5rem;
  }
}
.blog-post {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .blog-post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-post:last-of-type {
  margin-bottom: 0;
}

.meta-information {
  font-size: 15px;
}
.meta-information span {
  color: #f2be01;
  font-weight: 700;
}

.post-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .post-text a {
    margin-top: 0;
  }
}
.post-text h4 {
  margin: 0;
  line-height: 1;
}
.post-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.4rem;
  background-color: #8adb00;
  margin-top: 1rem;
}

.no-webp .home {
  background-image: url("../img/header.jpg");
}

.webp .home {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #2b2b2b;
  padding: 1rem 0 3rem 0;
}
.header .navbar a img {
  height: 90px;
}
.header.home {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.home .header-content {
    min-height: 70rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  font-size: 50px;
  line-height: 1.5;
  max-width: 56rem;
  padding-bottom: 4rem;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right {
    align-items: flex-end;
  }
}
.right .dark-mode-button {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.right .dark-mode-button:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .right .dark-mode-button {
    margin-top: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegation a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegation a:last-of-type {
    margin-right: 0;
  }
}
.navegation a:hover {
  color: #f2be01;
}

/** Hamburguer Menu **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.testimonial {
  background-color: #8adb00;
  font-size: 2.2rem;
  padding: 2rem;
  color: #ffffff;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
  font-style: italic;
  font-size: 16px;
  margin-right: 3rem;
}